const axios = require("axios");

export const getResponse = async (api_key, prompt) => {
    let form = {
        prompt: `${prompt}\n`,
        n: 5,
        size: "1024x1024"
    };
    let headers = {
        headers: {
            Authorization: `Bearer ${api_key}`,
            'Content-Type': 'application/json'
        },
    };
    try {
        let response = await axios.post(`https://api.openai.com/v1/images/generations`, form, headers);
        console.log("response", response)
        return response?.data?.data
    } catch (err) {
        console.log("error",err)
        return err.response.data.error.type;
    }
}

