<template>
  <v-responsive>
    <v-parallax src="https://cdn.vuetifyjs.com/images/parallax/material2.jpg">
      <v-container>
        <v-form
          ref="form"
          v-model="valid"
          lazy-validation
          class="pa-10 d-flex align-center"
        >
          <v-text-field
            v-model="imagePrompt"
            label="Image Description"
            required
            class="bg-white"
          ></v-text-field>
          <div class="d-flex flex-column">
            <v-btn
              color="success"
              class="mr-4 ma-1"
              @click="getAIResponse"
              :disabled="btnDisable"
            >
              Generate
            </v-btn>
            <v-btn
              color="grey"
              class="mr-4 ma-1"
              @click="imagePrompt = ''"
              :disabled="btnDisable"
            >
              Reset
            </v-btn>
          </div>
        </v-form>
        <div
          v-if="this.$store.state.ai_responses.length"
          class="pa-12 bg-amber-lighten-5"
        >
          <v-row align="center" v-if="AIResponse.length">
            <v-col
              v-for="(item, index) in AIResponse"
              :key="index"
            >
              <v-card>
                <v-img class="bg-white" :src="item.url" cover></v-img>
              </v-card>
            </v-col> </v-row
          ><v-row align="center" v-else>
            <v-col
              v-for="(item, index) in this.$store.state.ai_responses"
              :key="index"
            >
              <v-card>
                <v-img class="bg-white" :src="item.url" cover></v-img>
              </v-card>
            </v-col>
          </v-row>
        </div>
      </v-container>
    </v-parallax>
  </v-responsive>
</template>
<script>
import { getResponse } from "../js/utils/ai";
export default {
  name: "HomePage",
  data() {
    return {
      valid: true,
      imagePrompt: "",
      promptRules: [(v) => !!v || "Descripton is required"],
      btnDisable: false,
      loading: false,
      AIResponse: [],
    };
  },
  mounted() {
    console.log("$store", this.$store.state.apiKey);
  },
  methods: {
    async getAIResponse() {
      if (!this.imagePrompt) return;
      this.btnDisable = true;
      this.loading = true;
      const { valid } = await this.$refs.form.validate();
      if (valid) {
        this.AIResponse = await getResponse(
          this.$store.state.apiKey,
          this.imagePrompt
        );
      }
      this.btnDisable = false;
      console.log("valid", this.AIResponse);
    },
  },
};
</script>